const statusesSchema = {
	type: 'object',
	properties: {
		type: {type: 'string', enum: ['badge', 'presence', 'icon']},
		icon: {type: 'string'},
		label: {type: 'string', translatable: true},
		presence: {type: 'string', enum: ['available', 'busy', 'away', 'offline']},
		status: {
			type: 'string',
			enum: [
				'critical',
				'high',
				'warning',
				'moderate',
				'info',
				'positive',
				'low'
			]
		},
		value: {type: 'number'},
		round: {type: 'boolean'},
		description: {type: 'string', translatable: true}
	}
};

const actionSchema = {
	type: 'object',
	properties: {
		icon: {type: 'string'},
		label: {type: 'string', translatable: true},
		clickActionType: {type: 'string'},
		mouseenterActionType: {type: 'string'},
		mouseleaveActionType: {type: 'string'}
	}
};

const highlightedValueSchema = {
	type: 'object',
	properties: {
		label: {type: 'string', translatable: true},
		status: {type: 'string'},
		width: {type: 'string'}
	}
};

const identifierSchema = {
	type: 'object',
	properties: {
		type: {type: 'string', enum: ['icon', 'avatar']},
		icon: {type: 'string'},
		userName: {type: 'string'},
		imageSrc: {type: 'string'},
		ariaHidden: {type: 'string', enum: ['true', 'false']}
	}
};

export const itemsSchema = {
	$id: 'itemsSchema',
	type: 'array',
	items: {
		type: 'object',
		properties: {
			id: {type: ['string', 'number']},
			label: {type: 'string', translatable: true},
			children: {type: 'array', items: {$ref: 'itemsSchema#/items'}},
			childrenAvailable: {type: 'number'},
			identifierProps: identifierSchema,
			actionable: {type: 'boolean'},
			actions: {type: 'array', items: actionSchema},
			statuses: {type: 'array', items: statusesSchema},
			highlightedValueProps: {type: 'array', items: highlightedValueSchema},
			disabled: {type: 'boolean'},
			variant: {type: 'string', enum: ['placeholder']}
		}
	}
};
