import isEqual from 'lodash/isEqual';

export function hasPath(list, path) {
	return list.findIndex((p) => isEqual(p, path)) > -1;
}
export function hasChildPath(list, path) {
	return (
		list.findIndex(
			(p) =>
				p.length === path.length + 1 && isEqual(p.slice(0, path.length), path)
		) > -1
	);
}
export function removePath(list, path) {
	return list.filter((p) => !isEqual(p, path));
}
export function addPath(list, path) {
	return [...list, path];
}
export function togglePath(list, path) {
	return hasPath(list, path) ? removePath(list, path) : addPath(list, path);
}
export function isParent(parentPath, childPath) {
	return (
		Array.isArray(parentPath) &&
		Array.isArray(childPath) &&
		parentPath.length < childPath.length &&
		parentPath.reduce((acc, n, i) => acc && n === childPath[i], true)
	);
}
export function rebasePath(list, oldRoot, newRoot) {
	return list.map((nextPath) =>
		isEqual(nextPath.slice(0, oldRoot.length), oldRoot)
			? [...newRoot, ...nextPath.slice(oldRoot.length - 1)]
			: nextPath
	);
}
export function getPathToItem(items, itemId) {
	if (!Array.isArray(items) || items.length === 0) return [];
	const thisLevelItem = items.find((item) => item.id === itemId);
	if (thisLevelItem) return [thisLevelItem.id];
	for (const item of items) {
		const childPath = getPathToItem(item.children, itemId);
		if (childPath && childPath.length) return [item.id, ...childPath];
	}
	return [];
}
export function getPathItem(items, path) {
	let nextLevel = items;
	return path.reduce((acc, nextId) => {
		const nextItem = nextLevel.find((i) => i.id === nextId);
		nextLevel = nextItem ? nextItem.children : [];
		return nextItem;
	}, null);
}
function countItemsInPath(path, items) {
	let item = getPathItem(items, path);
	let count = 1;
	if (item.children) {
		item.children.forEach((child) => {
			count += countItemsInPath([...path, child.id], items);
		});
	}
	return count;
}
export function countItemsInPaths(paths, items) {
	if (!paths || paths.length === 0) return 0;
	return paths.reduce((acc, path) => acc + countItemsInPath(path, items), 0);
}
export function spliceItemsAtPath(
	items,
	path,
	deleteCount,
	insertItems,
	below = false
) {
	if (
		!Array.isArray(path) ||
		!Array.isArray(items) ||
		path.length === 0 ||
		items.length === 0
	)
		return undefined;
	let itemsToSplice = items;
	for (let i = 0; i < path.length - 1; i++) {
		const parentIndex = itemsToSplice.findIndex((j) => j.id === path[i]);
		if (parentIndex < 0) return undefined;
		itemsToSplice = itemsToSplice[parentIndex].children;
	}
	if (!itemsToSplice || itemsToSplice === 0) return undefined;
	let itemIndex = itemsToSplice.findIndex(
		(i) => i.id === path[path.length - 1]
	);
	if (itemIndex < 0) return undefined;
	if (below) itemIndex++;
	if (deleteCount > 0) {
		return itemsToSplice.splice(itemIndex, deleteCount);
	}
	return itemsToSplice.splice(itemIndex, 0, ...insertItems);
}
