import {
	getFocusedElement,
	getTreeRoot,
	getVisibleTreeItems,
	getSelectedTreeItems,
	getHostTreeItem,
	getPreviousTreeItemAction,
	getNextTreeItemAction,
	getTreeItemStatusActions,
	isTreeItem,
	isTreeItemLoadMore,
	isTreeItemLoadIndicator
} from './elements';

const hostToLastFocusedTreeItem = new WeakMap();

function focusTreeItem(treeItem) {
	if (isTreeItem(treeItem) || isTreeItemLoadMore(treeItem)) {
		treeItem.focus();
		return;
	}
}

export function changeItemActionsVisibilityIfNeeded(host, treeItem) {
	const lastTreeItem = getLastFocusedTreeItem(host);
	if (
		lastTreeItem &&
		lastTreeItem.firstChild.classList.contains('show-actions')
	) {
		const lastStatusActions = getTreeItemStatusActions(lastTreeItem);
		lastStatusActions.forEach((el) => (el.style.visibility = ''));
	}
	if (treeItem && treeItem.firstChild.classList.contains('show-actions')) {
		const statusActions = getTreeItemStatusActions(treeItem);
		statusActions.forEach((el) => (el.style.visibility = 'visible'));
	}
}

export function setLastFocusedTreeItem(host, treeItem) {
	changeItemActionsVisibilityIfNeeded(host, treeItem);
	hostToLastFocusedTreeItem.set(host, treeItem);
}

export function getLastFocusedTreeItem(host) {
	return hostToLastFocusedTreeItem.get(host);
}

export function focusInitialTreeItem(host) {
	const lastFocusedItem = getLastFocusedTreeItem(host);
	if (lastFocusedItem) {
		lastFocusedItem.focus();
		return;
	}
	const selectedItems = getSelectedTreeItems(host);
	if (selectedItems.length) {
		selectedItems[0].focus();
		return;
	}
	const visibleItems = getVisibleTreeItems(host);
	if (visibleItems.length) {
		visibleItems[0].focus();
	}
}

export function focusFirstTreeItem(host) {
	const treeItems = getVisibleTreeItems(host);
	focusTreeItem(treeItems[0]);
}

export function focusLastTreeItem(host) {
	const treeItems = getVisibleTreeItems(host);
	focusTreeItem(treeItems[treeItems.length - 1]);
}

export function isLastSubtreeItemFocused(host) {
	const focused = getFocusedElement(host);
	const siblings = [...focused.parentNode.children];
	return siblings.indexOf(focused) === siblings.length - 1;
}
export function getNextFocusableTreeItem(host) {
	const focused = getFocusedElement(host);
	const treeRoot = getTreeRoot(host);
	const treeItems = getVisibleTreeItems(host);
	if (focused === treeRoot) {
		// root of tree is focused, return first tree item
		return treeItems[0];
	}
	const focusedItem = getHostTreeItem(focused);
	const focusedItemIndex = treeItems.findIndex((node) => node === focusedItem);
	if (treeItems.length === focusedItemIndex + 1) {
		// last tree item is focused, return nothing
		return null;
	}

	if (isTreeItemLoadIndicator(treeItems[focusedItemIndex + 1])) {
		// loader is focused, skip it
		return treeItems[focusedItemIndex + 2];
	}
	// otherwise, focus next item
	return treeItems[focusedItemIndex + 1];
}
export function focusNextTreeItem(host) {
	const nextItem = getNextFocusableTreeItem(host);
	if (nextItem) {
		focusTreeItem(nextItem);
	}
}

export function getPrevFocusableTreeItem(host) {
	const focused = getFocusedElement(host);
	const treeRoot = getTreeRoot(host);
	const treeItems = getVisibleTreeItems(host);
	if (focused === treeRoot) {
		// root of tree is focused, do nothing
		return null;
	}
	const focusedItem = getHostTreeItem(focused);
	const focusedItemIndex = treeItems.findIndex((node) => node === focusedItem);
	if (focusedItemIndex === 0) {
		// first tree item is focused, do nothing
		return null;
	}
	if (isTreeItemLoadIndicator(treeItems[focusedItemIndex - 1])) {
		// loader is focused, skip it
		return treeItems[focusedItemIndex - 2];
	}
	// otherwise, focus next item
	return treeItems[focusedItemIndex - 1];
}

export function focusPreviousTreeItem(host) {
	const nextItem = getPrevFocusableTreeItem(host);
	if (nextItem) {
		focusTreeItem(nextItem);
	}
}

export function focusPreviousTreeItemAction(treeItem, focused) {
	const element = getPreviousTreeItemAction(treeItem, focused);
	if (element) {
		element.focus();
	}
}

export function focusNextTreeItemAction(treeItem, focused) {
	const element = getNextTreeItemAction(treeItem, focused);
	if (element) {
		element.focus();
	}
}
