export function getFocusedElement(host) {
	return host.shadowRoot.activeElement;
}

export function getTreeRoot(host) {
	return host.shadowRoot.querySelector('ul[role="tree"]');
}

export function getVisibleTreeItems(host) {
	return [
		...getTreeRoot(host).querySelectorAll(
			'li[data-visible="true"]:not([data-disabled="true"]), li[data-visible="true"][data-parent="true"]'
		)
	];
}

export function getSelectedTreeItems(host) {
	return [
		...getTreeRoot(host).querySelectorAll(
			'li[data-selected="true"][data-visible="true"]'
		)
	];
}

export function getHostTreeItem(el) {
	if (isTreeItem(el) || isTreeItemLoadMore(el)) {
		return el;
	}
	if (!el.parentElement) {
		return null;
	}
	return getHostTreeItem(el.parentElement);
}

export function getTreeItemTrigger(el) {
	return el.querySelector('.now-content-tree-trigger');
}

export function getTreeItemActions(el) {
	return [
		...el
			.querySelector('.now-content-tree-node')
			.querySelectorAll(
				'now-button-iconic.now-content-tree-status:not([disabled]), now-button-bare:not([disabled]), .now-content-tree-button:not([disabled])'
			)
	];
}
export function getTreeItemStatusActions(el) {
	return [
		...el
			.querySelector('.now-content-tree-node')
			.querySelectorAll('.now-content-tree-status:not([disabled])')
	];
}

export function getPreviousTreeItemAction(el, focused) {
	const actions = getTreeItemActions(el);
	if (el === focused) {
		// tree item is focused, return null
		return null;
	}
	const index = actions.findIndex((node) => node === focused);
	if (index === 0) {
		return el;
	}
	return actions[index - 1] || null;
}

export function getNextTreeItemAction(el, focused) {
	const actions = getTreeItemActions(el);
	if (el === focused) {
		// tree item is focused, return first item
		return actions[0];
	}
	const index = actions.findIndex((node) => node === focused);
	return actions[index + 1] || null;
}

export function isTreeItemParent(el) {
	return el.getAttribute('data-parent') === 'true';
}

export function isTreeItemExpanded(el) {
	return el.getAttribute('aria-expanded') === 'true';
}

export function getFirstTreeItemInPath(path) {
	return path.find(
		(node) =>
			node.nodeType === Node.ELEMENT_NODE &&
			node.getAttribute('role') === 'treeitem'
	);
}

export function isTreeItem(el) {
	return el.getAttribute('role') === 'treeitem';
}

export function isTreeItemLoadMore(el) {
	return el.getAttribute('data-id') === 'loadmore';
}

export function isTreeItemLoadIndicator(el) {
	return el.querySelector('.now-content-tree-node.is-loader');
}
