export function getShadowRootHost(node) {
	if (node.parentNode instanceof ShadowRoot) {
		return node.parentNode.host;
	}
	if (node.parentElement) {
		return getShadowRootHost(node.parentElement);
	}
	return null;
}

export function getParentItem(host, node) {
	if (node === host) return null;
	let nextEl = node;
	while (nextEl && nextEl !== host) {
		if (nextEl.parentElement) {
			nextEl = nextEl.parentElement;
		} else if (
			nextEl.parentNode &&
			nextEl.parentNode instanceof DocumentFragment
		) {
			nextEl = nextEl.parentNode.host;
		} else {
			nextEl = null;
		}
		if (nextEl.tagName === 'LI') {
			break;
		}
	}
	return nextEl === host ? null : nextEl;
}
