import {
	getPathToItem,
	isParent,
	spliceItemsAtPath,
	rebasePath,
	getPathItem
} from './paths';
import {
	getFocusedElement,
	getVisibleTreeItems,
	isTreeItemExpanded
} from './elements';
import {getParentItem} from './dom';
import {isLastSubtreeItemFocused, getNextFocusableTreeItem} from './focus';

export function hideDragCursor(host) {
	const cursor = host.shadowRoot.querySelector('.now-content-tree-dragdrop');
	cursor.style.visibility = 'hidden';
	const curParents = host.shadowRoot.querySelectorAll(
		'li.now-content-tree-drop-target-parent'
	);
	curParents.forEach((p) =>
		p.classList.remove('now-content-tree-drop-target-parent')
	);
}
export function cleanupDragDrop(coeffects) {
	const {host} = coeffects;
	hideDragCursor(host);
}

export function showDragCursor(
	dragContext,
	host,
	target,
	itemPath,
	cursorAbove
) {
	const cursor = host.shadowRoot.querySelector('.now-content-tree-dragdrop');
	if (!cursor) return;
	const targetBounds = target.getBoundingClientRect();
	cursor.style.left = `${targetBounds.left + window.scrollX}px`;
	cursor.style.top = `${(cursorAbove ? targetBounds.top : targetBounds.bottom) +
		window.scrollY -
		4}px`;
	cursor.style.width = `${targetBounds.width}px`;
	cursor.style.visibility = 'visible';
	// hilite parent
	const curParents = host.shadowRoot.querySelectorAll(
		'li.now-content-tree-drop-target-parent'
	);
	const parent =
		itemPath.length > 1
			? host.shadowRoot.getElementById(itemPath[itemPath.length - 2])
			: null;
	curParents.forEach((p) => {
		if (p !== parent) p.classList.remove('now-content-tree-drop-target-parent');
	});
	if (parent) {
		parent.classList.add('now-content-tree-drop-target-parent');
	}
	// move dragImage too, if needed
	moveDragImage(dragContext, target, cursorAbove);
}
export function isValidDrop(target, context, properties) {
	if (!target.id || target.id === '') return false;
	const targetPath = getPathToItem(properties.items, target.id);
	const targetPathFlat = targetPath.join('_');
	const items = context.getData('now-nds/content-tree-item-path');
	// don't allow dropping an item on itself.
	const itemsIncludesTarget = items.reduce(
		(acc, item) => acc || targetPathFlat === item.join('_'),
		false
	);
	return (
		!itemsIncludesTarget &&
		context.isValidDropTarget(target) &&
		Array.isArray(items) &&
		items.length > 0 &&
		items.reduce((acc, item) => acc && !isParent(item, targetPath), true)
	);
}
function moveDragImage(dragContext, target, cursorAbove) {
	// move dragImage too, if needed
	if (dragContext.getType() === 'mouse') return;
	const targetBounds = target.getBoundingClientRect();
	const item = target.querySelector('div.now-content-tree-node');
	const itemHeight = item
		? item.getBoundingClientRect().height
		: targetBounds.height;
	const xPos = targetBounds.left + 32;
	const yPos = cursorAbove
		? targetBounds.top + 6
		: targetBounds.bottom - (itemHeight - 6);
	dragContext.moveDragImage(xPos, yPos);
}
export function updateDragCursor(dragContext, coeffects, cursorAbove) {
	const {action, host, properties, updateState} = coeffects;
	const isKeyboard = dragContext.getType() === 'key';
	const target = isKeyboard ? getFocusedElement(host) : action.payload.target;
	const itemPath = getPathToItem(properties.items, target.id);
	updateState({
		path: 'dragdropState.focusId',
		operation: 'set',
		value: target.id,
		shouldRender: false
	});
	updateState({
		path: 'dragdropState.cursorAbove',
		operation: 'set',
		value: cursorAbove,
		shouldRender: false
	});
	if (isValidDrop(target, dragContext, properties)) {
		showDragCursor(dragContext, host, target, itemPath, cursorAbove);
	} else {
		hideDragCursor(host);
	}
}
export function handleKeyboardDrag(dragContext, coeffects) {
	const {host, action, state, properties, updateState} = coeffects;
	const {key} = action.payload.event;
	const target = getFocusedElement(host);
	const targetList = target.parentNode;
	const listIndex = Array.from(targetList.children).findIndex(
		(i) => i === target
	);
	const parentItem = getParentItem(host, target);
	const allItems = getVisibleTreeItems(host);
	const isValidDropTarget = isValidDrop(target, dragContext, properties);
	const hasChildren =
		isTreeItemExpanded(target) && target.querySelector('now-collapse') !== null;
	const oldState = state.dragdropState;
	const newState = {...state.dragdropState};

	// compute new state.
	// for logic, see: https://miro.com/app/board/o9J_krp4dEU=/?moveToWidget=3074457347344093553&cot=13
	if (key === 'Home') {
		newState.focusId = allItems[0].id;
		newState.cursorAbove = true;
	} else if (key === 'End') {
		newState.focusId = allItems[allItems.length - 1].id;
		newState.cursorAbove = false;
	} else if (key === 'ArrowUp') {
		const targetIndex = allItems.findIndex((i) => i === target);
		// insertion cursor above an item
		if (!isValidDropTarget || oldState.cursorAbove) {
			// ...and it is not the first item
			if (listIndex > 0) {
				const prevSibling = targetList.children[listIndex - 1];
				const prevSiblingList = prevSibling.querySelector('ul');
				const prevSiblingExpanded = isTreeItemExpanded(prevSibling);
				//  ...and the previous item has children and is expanded, so set cursor below previous item's last child
				if (prevSibling && prevSiblingExpanded && prevSiblingList) {
					newState.focusId =
						prevSiblingList.children[prevSiblingList.children.length - 1].id;
					newState.cursorAbove = false;
					// ...if no children, set cursor above previous sibling
				} else {
					newState.focusId = allItems[Math.max(0, targetIndex - 1)].id;
					newState.cursorAbove = true;
				}
			} else {
				newState.focusId = allItems[Math.max(0, targetIndex - 1)].id;
				newState.cursorAbove = true;
			}
		} else if (hasChildren && !oldState.cursorAbove) {
			const kids = target.querySelector('ul').children;
			newState.focusId = kids[kids.length - 1].id;
			newState.cursorAbove = false;
		} else {
			newState.focusId = oldState.focusId;
			newState.cursorAbove = true;
		}
	} else if (key === 'ArrowDown') {
		const isLastSubtreeItem = isLastSubtreeItemFocused(host);
		const nextItem = getNextFocusableTreeItem(host);
		const grandParentItem = parentItem ? getParentItem(host, parentItem) : null;
		const parentList = parentItem ? parentItem.parentNode : null;
		const parentIndex = parentItem
			? Array.from(parentList.children).findIndex((i) => i === parentItem)
			: null;
		const parentNextSibling = parentItem
			? parentList.children[
					Math.min(parentIndex + 1, parentList.children.length - 1)
			  ]
			: null;
		// if we're above an expanded node, move into the subtree
		if (!isValidDropTarget || (hasChildren && oldState.cursorAbove)) {
			newState.focusId = nextItem ? nextItem.id : oldState.focusId;
			newState.cursorAbove = true;
		} else if (!isLastSubtreeItem || (!oldState.cursorAbove && !parentItem)) {
			const nextSibling =
				targetList.children[
					Math.min(listIndex + 1, targetList.children.length - 1)
				];
			// if we're not at the end of this subtree or it's a leaf node, go to the next sibling
			if (listIndex < targetList.children.length - 1) {
				newState.focusId = nextSibling.id;
				newState.cursorAbove = true;
			}
			// if it's the last subtree item and the cursor is above, move cursor to the end
		} else if (isLastSubtreeItem && oldState.cursorAbove) {
			newState.focusId = oldState.focusId;
			newState.cursorAbove = false;
			// if it's the last subtree items and the cursor is below, move to below our parent
		} else if (
			isLastSubtreeItem &&
			!oldState.cursorAbove &&
			parentItem &&
			grandParentItem &&
			!parentNextSibling
		) {
			newState.focusId = parentItem.id;
			newState.cursorAbove = false;
			// if it's the last subtree items and the cursor is below, move to our parent's next sibling
		} else if (isLastSubtreeItem && !oldState.cursorAbove && parentItem) {
			newState.focusId = parentNextSibling.id;
			newState.cursorAbove = parentIndex < parentList.children.length - 1;
		}
	}

	// save the state and schedule the cursor update
	updateState({
		path: 'dragdropState',
		operation: 'set',
		value: newState,
		shouldRender: false
	});

	// put focus on correct node
	const itemToFocus = host.shadowRoot.querySelector(`#${newState.focusId}`);
	itemToFocus.focus();

	// update insertion cursor and drag image
	const newItemPath = getPathToItem(properties.items, newState.focusId);
	const newItemValid = isValidDrop(itemToFocus, dragContext, properties);
	if (newItemValid) {
		showDragCursor(
			dragContext,
			host,
			itemToFocus,
			newItemPath,
			newState.cursorAbove
		);
	} else {
		hideDragCursor(host);
	}
	moveDragImage(dragContext, itemToFocus, newState.cursorAbove);
}

export function handleDrop(coeffects) {
	const {action, properties, dispatch, state} = coeffects;
	const {target, context} = action.payload;
	const {dragdropState} = state;

	// FF will sometimes not fire dragend if there are dom manipulations, so cleanup here too.
	// https://bugzilla.mozilla.org/show_bug.cgi?id=460801
	cleanupDragDrop(coeffects);

	if (!isValidDrop(target, context, properties)) return;

	// move the items
	const items = context.getData('now-nds/content-tree-item-path');
	const newItems = [...properties.items];
	const itemsToMove = [];
	items.forEach((nextItem) => {
		const itemToMove = spliceItemsAtPath(newItems, nextItem, 1);
		if (itemToMove && itemToMove.length > 0) {
			itemsToMove.push(itemToMove[0]);
		}
	});
	const toPath = getPathToItem(newItems, target.id);
	spliceItemsAtPath(
		newItems,
		toPath,
		0,
		itemsToMove,
		!dragdropState.cursorAbove
	);
	dispatch('NOW_CONTENT_TREE#ITEMS_SET', {name: 'items', value: newItems});

	// update selectedItems
	const newParent = toPath.slice(0, toPath.length - 1);
	let newExpanded = [...properties.expandedItems];
	items.forEach((oldRoot) => {
		newExpanded = rebasePath(newExpanded, oldRoot, newParent);
	});
	// remove newly childless nodes
	for (let i = newExpanded.length - 1; i >= 0; i--) {
		const nextExpPath = newExpanded[i];
		const nextExp = getPathItem(newItems, nextExpPath);
		if (!nextExp || !nextExp.children || nextExp.children.length === 0) {
			newExpanded.splice(i, 1);
		}
	}
	dispatch('NOW_CONTENT_TREE#EXPANDED_ITEMS_SET', {value: newExpanded});

	// update expandedItems
	let newSelected = [...properties.selectedItems];
	items.forEach((oldRoot) => {
		newSelected = rebasePath(newSelected, oldRoot, newParent);
	});
	dispatch('NOW_CONTENT_TREE#SELECTED_ITEMS_SET', {value: newSelected});
}
