import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {getActiveElement} from '@servicenow/library-enhanced-utils';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import styles from './_now-content-tree.scss';
import '@servicenow/now-collapse';
import focusBehavior from '@servicenow/behavior-focus';
import dragdropBehavior, {
	getDragContext,
	cancelDrag
} from '@servicenow/behavior-dragdrop';
import '@servicenow/now-avatar';
import '@servicenow/now-button';
import '@servicenow/now-highlighted-value';
import '@servicenow/now-icon';
import '@servicenow/now-badge';
import '@servicenow/now-loader';
import '@servicenow/now-tooltip';
import {
	schedule,
	unschedule,
	createTooltipActionHandler
} from '@servicenow/now-tooltip';
import isEqual from 'lodash/isEqual';
import omit from 'lodash/omit';
import {t} from 'sn-translate';
import {getShadowRootHost} from './utils/dom';
import {
	focusInitialTreeItem,
	focusFirstTreeItem,
	focusLastTreeItem,
	focusNextTreeItem,
	focusPreviousTreeItem,
	focusPreviousTreeItemAction,
	focusNextTreeItemAction,
	setLastFocusedTreeItem,
	changeItemActionsVisibilityIfNeeded
} from './utils/focus';
import {
	getFocusedElement,
	getHostTreeItem,
	getTreeItemTrigger,
	isTreeItemExpanded,
	isTreeItemParent,
	getFirstTreeItemInPath,
	getNextTreeItemAction,
	getPreviousTreeItemAction,
	isTreeItemLoadMore
} from './utils/elements';
import {
	getPathItem,
	getPathToItem,
	togglePath,
	hasPath,
	hasChildPath
} from './utils/paths';
import highlightSearchTerm from './utils/highlight';
import {itemsSchema} from './schemas/content-tree-schema';
import {actionTypes} from '@servicenow/ui-core';
import {
	cleanupDragDrop,
	updateDragCursor,
	hideDragCursor,
	handleKeyboardDrag,
	isValidDrop,
	handleDrop
} from './utils/dragdrop';

const {COMPONENT_DISCONNECTED} = actionTypes;

// delay when hovering over collapsed item during drag operation before item is expanded (ms)
const AUTO_EXPAND_DELAY = 1250;

const showTooltip = (updateState, item, target) => {
	updateState({
		path: 'currentTooltip',
		operation: 'set',
		value: {ref: target, content: item.label, opened: true}
	});
};

const hideTooltip = (updateState) => {
	updateState({
		path: 'currentTooltip',
		operation: 'set',
		value: {ref: null, content: '', opened: false}
	});
};

const expandItem = (item, path, dispatch, properties) => {
	const {children = [], childrenAvailable = 0} = item;
	const hasUnloadedChildren = childrenAvailable > children.length;
	const parent = children.length > 0 || hasUnloadedChildren;
	const loading = parent && getLoadingState(properties.loadingItems, path);

	if (
		hasUnloadedChildren &&
		children.length === 0 &&
		loading.status === 'inactive'
	) {
		dispatch(() => {
			return {
				type: 'NOW_CONTENT_TREE#LOADING_REQUESTED',
				payload: {item, path}
			};
		});
	}
	// Parent has some children, open to show them
	dispatch(({properties}) => ({
		type: 'NOW_CONTENT_TREE#EXPANDED_ITEMS_SET',
		payload: {value: togglePath(properties.expandedItems, path)}
	}));
};

const renderTreeItemLoadIndicator = ({showDividers, visible}) => {
	return (
		<li tabindex="-1" role="none" data-visible={visible}>
			<div
				class={{
					'now-content-tree-node': true,
					'show-dividers': showDividers
				}}>
				<div className="now-content-tree-leaf">
					<now-loader className="now-m-inline--sm" size="md" />
					<span className="now-line-height-crop">
						<span className="now-content-tree-label -loader">
							{t('Loading...')}
						</span>
					</span>
				</div>
			</div>
		</li>
	);
};

const renderTreeItemLoadMore = ({
	error,
	errorMessage,
	showDividers,
	visible,
	onClick
}) => {
	const loadMoreMessage = t('Show More');
	const retryMessage = t('Retry');
	const retryAriaLabel = `${errorMessage}, ${retryMessage}`;
	const renderContent = !error ? (
		<now-button-bare
			className="now-m-inline-start--xxl"
			variant="secondary"
			label={loadMoreMessage}
			hide-padding
		/>
	) : (
		<div className="now-content-tree-leaf">
			<now-icon
				className="now-content-tree-icon -error"
				icon="exclamation-triangle-outline"
				size="md"
			/>
			<span className="now-line-height-crop">
				<span className="now-content-tree-label -error">{errorMessage}</span>
			</span>
			<now-button-bare
				className="now-m-inline-start--sm"
				tabindex="-1"
				variant="secondary"
				label={retryMessage}
				config-aria={{'aria-label': retryAriaLabel}}
				hide-padding
			/>
		</div>
	);

	return (
		<li tabindex="-1" role="none" data-id="loadmore" data-visible={visible}>
			<div
				class={{'now-content-tree-node': true, 'show-dividers': showDividers}}
				on-click={onClick}>
				{renderContent}
			</div>
		</li>
	);
};

/**
 * @param {{path: string[], error: boolean, errorMessage?: string}[]} loadingItems
 * @param {string[]} path
 * @return {{status: ("inactive"|"active"|"error"), errorMessage?: string}} state
 */
const getLoadingState = (loadingItems, path) => {
	const index = loadingItems.findIndex((item) => isEqual(item.path, path));
	if (index === -1) {
		return {status: 'inactive'};
	}
	const {error = false, errorMessage = t('Unable to load')} = loadingItems[
		index
	];
	return {status: !error ? 'active' : 'error', errorMessage};
};

// Creates custom aria label that reads the highlighted value label if defined
const getRowAriaLabel = (item) => {
	let label = item.label;
	if (item.highlightedValueProps) {
		const labels = item.highlightedValueProps
			.map((hvp) => hvp.label)
			.join(' - ');
		label += ` - ${labels}`;
	}
	if (item.disabled) {
		label += `, ${t('disabled')}`;
	}
	return label;
};

const getAriaSelected = (select, selected) => {
	if (select === 'multi') {
		return selected ? 'true' : 'false';
	} else if (select === 'single') {
		return selected ? 'true' : undefined;
	}
};

const onFocusItem = (e, item, componentId, updateState) => {
	const host = getShadowRootHost(e.target);
	// store the last focused item
	const lastFocused = getFirstTreeItemInPath(e.composedPath());
	setLastFocusedTreeItem(host, lastFocused);
	hideTooltip(updateState);
	// only show the tooltip if the label is really truncated
	const span = lastFocused.querySelector('span.now-content-tree-label');
	if (!span || span.offsetWidth >= span.scrollWidth) return;
	schedule(
		componentId,
		showTooltip.bind(null, updateState, item, lastFocused.firstChild)
	);
};

const renderTreeItem = ({
	item,
	path,
	select,
	showDividers,
	showActionsOnHover,
	showPresencePattern,
	highlightSelectedParent,
	expandedItems,
	selectedItems,
	loadingItems,
	dispatch,
	renderChildren,
	parentExpanded,
	searchTerm,
	size,
	triggerIcon,
	overflow,
	componentId,
	updateState
}) => {
	const {children = [], childrenAvailable = 0} = item;
	const hasUnloadedChildren = childrenAvailable > children.length;
	const parent = children.length > 0 || hasUnloadedChildren;
	const selected = hasPath(selectedItems, path);
	const childSelected = hasChildPath(selectedItems, path);
	const expanded = parent && hasPath(expandedItems, path);
	const loading = parent && getLoadingState(loadingItems, path);

	const onClickTrigger = () => {
		if (
			!expanded &&
			hasUnloadedChildren &&
			children.length === 0 &&
			loading.status === 'inactive'
		) {
			dispatch(() => {
				return {
					type: 'NOW_CONTENT_TREE#LOADING_REQUESTED',
					payload: {item, path}
				};
			});
		}
		if (expanded && loading.status !== 'inactive') {
			dispatch(() => {
				return {
					type: 'NOW_CONTENT_TREE#LOADING_CANCELLED',
					payload: {item, path}
				};
			});
		}
		// Parent has some children, open to show them
		dispatch(({properties}) => ({
			type: 'NOW_CONTENT_TREE#EXPANDED_ITEMS_SET',
			payload: {value: togglePath(properties.expandedItems, path)}
		}));
	};

	const onClickLoadMore = () => {
		if (!item.disabled) {
			dispatch(() => {
				return {
					type: 'NOW_CONTENT_TREE#LOADING_REQUESTED',
					payload: {item, path}
				};
			});
		}
	};

	const onToggleSelected = (e) => {
		const {
			tagName,
			parentNode: {tagName: parentTagName}
		} = e.target;
		if (
			e.target.classList.contains('now-content-tree-status') ||
			tagName === 'BUTTON' ||
			(tagName === 'NOW-ICON' && parentTagName === 'BUTTON') ||
			tagName === 'NOW-BUTTON-ICONIC' ||
			tagName === 'NOW-BUTTON-BARE'
		) {
			return;
		}
		if (!item.disabled) {
			dispatch(() => {
				return {
					type: 'NOW_CONTENT_TREE#ITEM_CLICKED',
					payload: {item}
				};
			});
		}
		if (item.disabled || select === 'none') {
			return;
		}
		dispatch(({properties}) => {
			let value;
			if (select === 'single') {
				value = hasPath(properties.selectedItems, path) ? [] : [path];
			}
			if (select === 'multi') {
				value = togglePath(properties.selectedItems, path);
			}
			return {
				type: 'NOW_CONTENT_TREE#SELECTED_ITEMS_SET',
				payload: {value}
			};
		});
	};

	const onClickItemLabel = () => {
		if (!item.disabled) {
			dispatch(() => {
				return {
					type: 'NOW_CONTENT_TREE#ACTIONABLE_ITEM_CLICKED',
					payload: {item}
				};
			});
		}
	};

	const onClickAction = (e, action) => {
		if (!item.disabled) {
			const actionTarget = getActiveElement();
			dispatch(action.clickActionType || 'NOW_CONTENT_TREE#ACTION_CLICKED', {
				item,
				action,
				domTarget: e.target,
				actionTarget
			});
		}
	};

	const onMouseenterAction = (e, action) => {
		if (!item.disabled) {
			dispatch(
				action.mouseenterActionType || 'NOW_CONTENT_TREE#ACTION_MOUSEENTER',
				{
					item,
					action,
					domTarget: e.target
				}
			);
		}
	};

	const onMouseleaveAction = (e, action) => {
		if (!item.disabled) {
			dispatch(
				action.mouseleaveActionType || 'NOW_CONTENT_TREE#ACTION_MOUSELEAVE',
				{
					item,
					action,
					domTarget: e.target
				}
			);
		}
	};

	const onMouseenterItem = (e) => {
		// see if the text is really overflowing
		const span = e.target.querySelector('span.now-content-tree-label');
		if (!span || span.offsetWidth >= span.scrollWidth) return;
		scheduleShowTooltip(e.target);
	};
	const scheduleShowTooltip = (target) => {
		hideTooltip(updateState);
		schedule(componentId, showTooltip.bind(null, updateState, item, target));
	};
	const scheduleHideTooltip = () => {
		schedule(componentId, hideTooltip.bind(null, updateState));
	};

	const triggerExpanded = {
		chevron: 'chevron-down-outline',
		caret: 'caret-down-fill',
		'plus-minus': 'minus-fill'
	};
	const triggerCollapsed = {
		chevron: 'chevron-end-outline',
		caret: 'caret-right-fill',
		'plus-minus': 'plus-fill'
	};
	const renderTrigger = parent ? (
		<now-button-iconic
			className="now-content-tree-trigger"
			icon={
				expanded ? triggerExpanded[triggerIcon] : triggerCollapsed[triggerIcon]
			}
			size={size}
			hide-padding
			bare
			title={t('Expand/Collapse')}
			variant="tertiary"
			config-aria={{button: {'aria-hidden': 'true'}}}
			tabindex="-1"
			on-click={onClickTrigger}
			disabled={item.disabled}
		/>
	) : (
		undefined
	);

	const renderBadgeStatus = (status) => (
		<now-badge
			className="now-content-tree-status"
			tabindex="-1"
			config-aria={{'aria-label': status.label}}
			disabled={item.disabled}
			hide-padding
			size="sm"
			value={status.value}
			round={status.round}
			status={status.status}
			data-test="status"
		/>
	);

	const renderPresenceStatus = (status) => (
		<now-icon-presence
			className="now-content-tree-status"
			tabindex="-1"
			config-aria={{'aria-label': status.presence}}
			disabled={item.disabled}
			presence={status.presence}
			show-pattern={showPresencePattern}
			hide-padding
			size={size}
			data-test="status"
		/>
	);

	const renderIconStatus = (status) => (
		<now-icon
			className="now-content-tree-status"
			tabindex="-1"
			config-aria={{'aria-label': status.label}}
			disabled={item.disabled}
			icon={status.icon}
			title={status.label}
			hide-padding
			size={size}
			data-test="status"
		/>
	);

	const renderStatuses =
		item.statuses &&
		item.statuses.map((status) => {
			switch (status.type) {
				case 'badge':
					return renderBadgeStatus(status);
				case 'presence':
					return renderPresenceStatus(status);
				default:
					return renderIconStatus(status);
			}
		});

	const renderActions =
		item.actions &&
		item.actions.map((action) => (
			<now-button-iconic
				className="now-content-tree-status"
				tabindex="-1"
				config-aria={{
					'aria-label': action.label
				}}
				bare
				disabled={item.disabled}
				hide-padding
				icon={action.icon || action.iconName}
				title={(action && action.label) || action.label}
				variant="tertiary"
				size={size}
				on-click={(e) => onClickAction(e, action)}
				on-mouseenter={(e) => onMouseenterAction(e, action)}
				on-mouseleave={(e) => onMouseleaveAction(e, action)}
				on-focus={(e) => onFocusItem(e, item, componentId, updateState)}
			/>
		));

	const renderHighlightedValues = item.highlightedValueProps
		? item.highlightedValueProps.map((hvp) => {
				return (
					<div
						style={{
							width: hvp.width ? hvp.width : null
						}}
						className="now-content-tree-highlighted-value-parent">
						<now-highlighted-value
							style={{
								'max-width': hvp.width ? hvp.width : null
							}}
							className="now-content-tree-highlighted-value"
							status={hvp.status}
							label={hvp.label}
							show-icon
						/>
					</div>
				);
		  })
		: undefined;

	const renderChildItems = children.length
		? renderChildren(
				item.children,
				path,
				parentExpanded && expanded,
				renderChildren
		  )
		: undefined;

	const renderLoadIndicatorItem =
		loading.status === 'active'
			? renderTreeItemLoadIndicator({
					showDividers,
					visible: parentExpanded && expanded
			  })
			: undefined;

	const loadingInactiveWithChildren =
		loading.status === 'inactive' && children.length > 0;

	const renderLoadMoreItem =
		hasUnloadedChildren &&
		(loading.status === 'error' || loadingInactiveWithChildren)
			? renderTreeItemLoadMore({
					error: loading.status === 'error',
					errorMessage: loading.errorMessage,
					showDividers,
					visible: parentExpanded && expanded,
					onClick: onClickLoadMore
			  })
			: undefined;

	const renderCollapseContent =
		renderChildItems || renderLoadIndicatorItem || renderLoadMoreItem ? (
			<now-collapse hidden={!expanded} expanded={expanded}>
				<ul
					className="now-content-tree-ul"
					role="group"
					data-test="parent-node">
					{renderChildItems}
					{renderLoadIndicatorItem}
					{renderLoadMoreItem}
				</ul>
			</now-collapse>
		) : (
			undefined
		);

	// Both `icon` and `iconName` are maintained for simplicity and backwards compatibility,
	// but `identifierProps` is more extensible to support avatar and other use cases
	const iconIdentifierName =
		item.identifierProps && item.identifierProps.icon
			? item.identifierProps.icon
			: item.icon || item.iconName;
	let renderIdentifier;

	if (item.identifierProps && item.identifierProps.type === 'avatar') {
		renderIdentifier = (
			<now-avatar
				{...omit(item.identifierProps, 'type')}
				show-presence-pattern={showPresencePattern}
				className="now-m-inline-end--sm"
				size="sm"
			/>
		);
	} else if (iconIdentifierName) {
		renderIdentifier = (
			<now-icon
				class={{
					'now-content-tree-icon': true,
					'-identifier': true,
					'is-actionable': item.actionable,
					'is-placeholder': item.variant === 'placeholder'
				}}
				icon={iconIdentifierName}
				size={size}
				data-test="identifier"
			/>
		);
	}

	const highlightedLabel = searchTerm
		? highlightSearchTerm(searchTerm, item.label)
		: item.label;

	const renderLabelText = item.actionable ? (
		<button
			class={{
				'now-content-tree-button': true,
				'is-placeholder': item.variant === 'placeholder',
				'is-disabled': item.disabled
			}}
			tabindex="-1"
			aria-label={item.label}
			on-click={onClickItemLabel}
			on-focus
			disabled={item.disabled}
			data-test="label">
			<span className="now-line-height-crop">
				<span className="now-content-tree-label">{highlightedLabel}</span>
			</span>
		</button>
	) : (
		<span className="now-line-height-crop">
			<span
				class={{
					'now-content-tree-label': true,
					'is-placeholder': item.variant === 'placeholder'
				}}
				aria-label={item.label}>
				{highlightedLabel}
			</span>
		</span>
	);

	const renderLabel = (
		<Fragment>
			{renderTrigger}
			<div
				class={{
					'now-content-tree-leaf': true,
					'is-leaf-node': !parent
				}}
				data-test="label">
				{renderIdentifier}
				{renderLabelText}
			</div>
		</Fragment>
	);

	return (
		<li
			key={item.id}
			id={item.id}
			role="treeitem"
			tabindex="-1"
			aria-expanded={!parent ? undefined : String(expanded)}
			data-parent={parent}
			data-visible={parentExpanded}
			data-disabled={item.disabled}
			data-selected={selected}
			aria-label={getRowAriaLabel(item)}
			aria-selected={getAriaSelected(select, selected)}
			data-testid={item.id}
			on-focusout={overflow === 'truncate' ? scheduleHideTooltip : null}
			on-focus={(e) => onFocusItem(e, item, componentId, updateState)}>
			<div
				class={{
					'now-content-tree-node': true,
					'is-disabled': item.disabled,
					'is-selected': selected || (childSelected && highlightSelectedParent),
					'is-selected-child': selected && highlightSelectedParent,
					'show-dividers': showDividers,
					'show-actions': showActionsOnHover
				}}
				on-mouseenter={overflow === 'truncate' ? onMouseenterItem : null}
				on-mouseleave={overflow === 'truncate' ? scheduleHideTooltip : null}
				on-click={onToggleSelected}>
				{renderLabel}
				{renderStatuses}
				{renderActions}
				{renderHighlightedValues}
			</div>
			{renderCollapseContent}
		</li>
	);
};

const view = (state, {dispatch, updateState}) => {
	const {
		items,
		select,
		showDividers,
		showActionsOnHover,
		showPresencePattern,
		highlightSelectedParent,
		expandedItems,
		selectedItems,
		loadingItems,
		searchTerm,
		size,
		overflow,
		triggerIcon
	} = state.properties;
	const {hasFocus, componentId} = state;

	const onFocusIn = (e) => {
		const host = getShadowRootHost(e.target);
		const keyedIntoTree = e.target === host.shadowRoot.querySelector('ul');
		const relatedTargetHost = e.relatedTarget
			? getShadowRootHost(e.relatedTarget)
			: null;
		const lastFocusNotInTree = relatedTargetHost !== host;
		if (lastFocusNotInTree) {
			if (keyedIntoTree) {
				focusInitialTreeItem(host);
			}
			updateState({hasFocus: true});
		}
	};

	const onFocusOut = (e) => {
		const host = getShadowRootHost(e.target);
		const relatedTargetHost = e.relatedTarget
			? getShadowRootHost(e.relatedTarget)
			: null;
		const nextFocusNotInTree = relatedTargetHost !== host;
		if (nextFocusNotInTree) {
			setTimeout(() => {
				changeItemActionsVisibilityIfNeeded(host);
				updateState({hasFocus: false});
				cancelDrag(host);
			}, 0);
		}
	};

	const renderItems = (treeItems, path, parentExpanded, renderChildren) =>
		treeItems.map((item) => {
			return renderTreeItem({
				item,
				path: [...path, item.id],
				select,
				showDividers,
				showActionsOnHover,
				showPresencePattern,
				highlightSelectedParent,
				expandedItems,
				selectedItems,
				loadingItems,
				dispatch,
				renderChildren,
				parentExpanded,
				searchTerm,
				size,
				overflow,
				triggerIcon,
				componentId,
				updateState
			});
		});

	return (
		<div
			class={{
				'now-content-tree': true,
				'will-wrap': overflow === 'wrap',
				'will-truncate': overflow === 'truncate',
				['-' + size]: size
			}}>
			<ul
				className="now-content-tree-ul"
				data-test="root-node"
				role="tree"
				tabindex={hasFocus ? '-1' : '0'}
				on-focusin={onFocusIn}
				on-focusout={onFocusOut}>
				{renderItems(items, [], true, renderItems)}
			</ul>
			<now-tooltip
				id="tooltip"
				manage-opened
				opened={state.currentTooltip.opened}
				target-ref={state.currentTooltip.ref}
				content={state.currentTooltip.content}
				delay={{show: 0, hide: 0}}
			/>
			<div className="now-content-tree-dragdrop">
				<div className="now-content-tree-dragdrop-head" />
				<div className="now-content-tree-dragdrop-tail" />
			</div>
		</div>
	);
};

/**
 * The content tree is a hierarchical component that represents the relationship between a parent and child nodes.
 * The content tree can be simple or complex, displaying a rich context and
 * actions.
 *
 * ```jsx
 * <now-content-tree
 *    show-dividers
 *    select="single"
 *    expanded-items={[['item1']]}
 *    selected-items={[['item1', 'item2']]}
 *    items={[{id: 'item1', label: 'Item 1', children: [{id: 'item2', label: 'Item 2'}]}]}
 *    />
 * ```
 *
 * @seismicElement now-content-tree
 * @summary A hierarchical component that represents the relationship between parent and child nodes.
 * @uib.label Content tree
 * @uib.icon content-tree
 * @uib.description Collapsible tree of selectable data
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties size, select, overflow, triggerIcon, highlightSelectedParent, showActionsOnHover, showDividers, showPresencePattern, items, expandedItems, selectedItems, loadingItems, searchTerm
 * @uib.actions NOW_CONTENT_TREE#ITEM_CLICKED, NOW_CONTENT_TREE#ACTIONABLE_ITEM_CLICKED, NOW_CONTENT_TREE#ACTION_CLICKED, NOW_CONTENT_TREE#ACTION_MOUSELEAVE, NOW_CONTENT_TREE#ACTION_MOUSEENTER, NOW_CONTENT_TREE#LOADING_REQUESTED, NOW_CONTENT_TREE#LOADING_CANCELLED
 */
createEnhancedElement('now-content-tree', {
	properties: {
		/**
		 * An array of tree item objects describing each item in the tree. Each item
		 * can have child items nested underneath it.
		 *
		 * @type {ContentTreeItem[]}
		 * @uib.required
		 * @uib.defaultValue [{"id": "p1", "label": "Tree parent item", "children": [{"id": "c1", "label": "Tree child item 1"}, {"id": "c2", "label": "Tree child item 2"}, {"id": "c3", "label": "Tree child item 3"}]}]
		 * @uib.typeMetadata.emptyStateBehavior EMPTY_ARRAY
		 */
		items: {default: [], manageable: true, schema: itemsSchema},
		/**
		 * An array of path arrays to each selected item. Path arrays are a list of
		 * strings or numeric IDs tracing the path from the root of the `items` tree to the
		 * selected item. Each value is the `id` of an item in that path.
		 * Use `manage-selected-items` to override the default behavior and handle
		 * the `NOW_CONTENT_TREE#SELECTED_ITEMS_SET` action manually.
		 * @type {Array.<Array.<(string|number)>>}
		 * @uib.description An array of path arrays to each selected item.
		 * @uib.fieldType json
		 */
		selectedItems: {
			default: [],
			manageable: true,
			schema: {
				type: 'array',
				items: {type: 'array', items: {type: ['string', 'number']}}
			}
		},
		/**
		 * An array of path arrays to each expanded item. Path arrays are a list of
		 * strings or numeric IDs tracing the path from the root of the `items` tree to the
		 * expanded item. Each value is the `id` of an item in that path.
		 * Use `manage-expanded-items` to override the default behavior and handle
		 * the `NOW_CONTENT_TREE#EXPANDED_ITEMS_SET` action manually.
		 * @type {Array.<Array.<(string|number)>>}
		 * @uib.description An array of path arrays to each expanded item
		 * @uib.fieldType json
		 * @uib.defaultValue [["p1"]]
		 */
		expandedItems: {
			default: [],
			manageable: true,
			schema: {
				type: 'array',
				items: {type: 'array', items: {type: ['string', 'number']}}
			}
		},
		/**
		 * A list of items that are actively loading or recently failed to
		 * load children from the server. Similar to other path arrays, except
		 * each path should be wrapped in an object with an optional error flag and
		 * error message. The action `NOW_CONTENT_TREE#LOADING_REQUESTED` is
		 * dispatched when new children are requested. Add the path from that action
		 * payload to the loading list.
		 * @type {{path: Array.<(string|number)>, error: boolean, errorMessage: (string|undefined)}[]}
		 * @uib.description A list of items that are actively being loaded or recently failed to load children from the server.
		 */
		loadingItems: {
			default: [],
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						path: {type: 'array', items: {type: ['string', 'number']}},
						error: {type: 'boolean'},
						errorMessage: {type: 'string', translatable: true}
					}
				}
			}
		},
		/**
		 * Determines what happens when a user clicks a selectable tree item.
		 * Choose from the following:
		 * - "single" (default): Only one item can be selected at a time.
		 * - "multi": Multiple items can be selected at the same time.
		 * - "none": No items can be selected; use for trees that only display data.
		 * @type {("single"|"multi"|"none")}
		 * @uib.label Selection
		 * @uib.description Determines what happens when a user clicks a selectable tree item.
		 */
		select: {
			default: 'single',
			schema: {
				type: 'string',
				enum: ['single', 'multi', 'none']
			}
		},
		/**
		 * When true, the parent of the current selection is highlighted (with a different highlight color) along with the selected item.
		 * @type {boolean}
		 * @uib.label Highlight immediate parent
		 * @uib.description Select to highlight the parent node automatically when the user selects a child node. The child node uses the secondary highlight style.
		 */
		highlightSelectedParent: {default: false, schema: {type: 'boolean'}},
		/**
		 * Determines the behavior when items are too wide to fit in the space given
		 * to the tree.
		 * Choose from the following:
		 * - "scroll" (default): The entire tree scrolls horizontally in its container.
		 * - "truncate": Item text is truncated and clipped. Side scrolling is not enabled.
		 * - "wrap": Item text wraps over multiple lines in the space available. Side scrolling is not enabled.
		 * @type {("scroll"|"truncate"|"wrap")}
		 * @uib.label Overflow
		 * @uib.description Defines the overflow behavior.
		 */
		overflow: {
			default: 'scroll',
			schema: {type: 'string', enum: ['scroll', 'truncate', 'wrap']}
		},
		/**
		 * Sets the item label and icon size.
		 * @type {('sm'|'md')}
		 * @uib.label Size
		 * @uib.description Sets the tree size.
		 */
		size: {
			default: 'md',
			schema: {type: 'string', enum: ['sm', 'md']}
		},
		/**
		 * When true, displays a border on the bottom of each tree item.
		 * @type {boolean}
		 * @uib.description Show a divider line on the bottom of each tree item.
		 */
		showDividers: {default: false, schema: {type: 'boolean'}},
		/**
		 * When true, shows actions and statuses on hover or focus.
		 * @type {boolean}
		 * @uib.label Hide icons on default
		 * @uib.description Shows actions and statuses on hover or focus.
		 */
		showActionsOnHover: {default: false, schema: {type: 'boolean'}},
		/**
		 * When true, displays any presence indicators in the content tree with a pattern to make
		 * them distinguishable by a means other than color.
		 * @type {boolean}
		 * @uib.description Displays presence indicators with a pattern to make them distinguishable by a means other than color.
		 */
		showPresencePattern: {default: false, schema: {type: 'boolean'}},
		/**
		 * If provided, all item labels containing this value have the
		 * matching portion highlighted. Matching is case-insensitive and
		 * applies to all occurrences within the item label. This does not
		 * filter the visible items that are rendered: parent components are
		 * responsible or maintaining the list of rendered items.
		 * @type {string}
		 * @uib.label Search Item
		 * @uib.description Item labels containing this value have the matching portion highlighted. Matching is case-insensitive and applies to all occurrences within the item label. This does not filter the visible items that are rendered: parent components are responsible for maintaining the list of rendered items.
		 */
		searchTerm: {schema: {type: 'string'}},
		/**
		 * Configures the type of each parent's expand/collapse icon.
		 *
		 * @type {('chevron'|'caret'|'plus-minus')}
		 * @uib.label Trigger Icon
		 * @uib.description Defines the symbol to expand tree items.
		 */
		triggerIcon: {
			default: 'chevron',
			schema: {type: 'string', enum: ['chevron', 'caret', 'plus-minus']}
		}
	},
	initialState: {
		hasFocus: false,
		dragdropState: {focusId: null, initialFocusId: null, cursorAbove: true},
		currentTooltip: {ref: null, content: '', opened: false}
	},
	view,
	styles,
	eventHandlers: [
		{
			events: ['keydown'],
			effect: (coeffects) => {
				const {host, action} = coeffects;
				const {key, shiftKey} = action.payload.event;
				const dragContext = getDragContext(host);
				if (!dragContext && !shiftKey && [' ', 'Enter'].includes(key)) {
					// Toggle selection or load more
					const focused = getFocusedElement(host);
					const treeItem = getHostTreeItem(focused);
					if (focused === treeItem) {
						action.preventDefault();
						focused.querySelector('.now-content-tree-node').click();
					}
					if (focused !== treeItem && isTreeItemLoadMore(treeItem)) {
						// the user pressed space/enter on the show more button,
						// move their focus to the previous item or it will be lost
						// when the new children are added to the tree
						action.preventDefault();
						focused.click();
						focusPreviousTreeItem(host);
					}
				} else if (['ArrowDown', 'ArrowUp', 'Home', 'End'].includes(key)) {
					// Move between rows
					action.preventDefault();
					if (dragContext) {
						handleKeyboardDrag(dragContext, coeffects);
						return;
					}
					if (key === 'ArrowDown') {
						focusNextTreeItem(host);
					}
					if (key === 'ArrowUp') {
						focusPreviousTreeItem(host);
					}
					if (key === 'Home') {
						focusFirstTreeItem(host);
					}
					if (key === 'End') {
						focusLastTreeItem(host);
					}
				} else if (['ArrowRight', 'ArrowLeft'].includes(key)) {
					// Move within a given row
					action.preventDefault();
					const focused = getFocusedElement(host);
					const treeItem = getHostTreeItem(focused);
					if (!treeItem) {
						return;
					}
					if (focused === treeItem) {
						// the tree item itself is focused
						const isParent = isTreeItemParent(treeItem);
						const isExpanded = isTreeItemExpanded(treeItem);
						if (key === 'ArrowLeft' && isParent && isExpanded) {
							getTreeItemTrigger(treeItem).click();
						}
						if (key === 'ArrowRight' && isParent && !isExpanded) {
							getTreeItemTrigger(treeItem).click();
						} else if (
							!dragContext &&
							key === 'ArrowRight' &&
							(!isParent || isExpanded)
						) {
							focusNextTreeItemAction(treeItem, focused);
						}
					}
					if (!dragContext && focused !== treeItem) {
						// one of the tree item child actions is focused
						if (key === 'ArrowLeft') {
							focusPreviousTreeItemAction(treeItem, focused);
						}
						if (key === 'ArrowRight') {
							focusNextTreeItemAction(treeItem, focused);
						}
					}
				} else if (key === 'Tab') {
					if (dragContext) {
						cancelDrag(host);
					}
					// Move focus
					const focused = getFocusedElement(host);
					const treeItem = getHostTreeItem(focused);
					if (!treeItem) {
						return;
					}
					if (!shiftKey) {
						const treeItemAction = getNextTreeItemAction(treeItem, focused);
						if (treeItemAction) {
							action.preventDefault();
							treeItemAction.focus();
						}
					}
					if (shiftKey) {
						const treeItemAction = getPreviousTreeItemAction(treeItem, focused);
						if (treeItemAction) {
							action.preventDefault();
							treeItemAction.focus();
						}
					}
				}
			}
		}
	],
	actionHandlers: {
		...createTooltipActionHandler(hideTooltip),
		[COMPONENT_DISCONNECTED]: ({action}) => {
			const componentId = action.meta.id;
			unschedule(componentId);
		},
		'NOW_TOOLTIP#OPENED_SET': ({action, updateState}) => {
			const {value, target} = action.payload;
			const componentId = action.meta.path[action.meta.path.length - 1];
			if (target === 'content' && value) {
				unschedule(componentId);
			}

			if ((target === 'content' || target === 'keyboard') && !value) {
				schedule(componentId, () => hideTooltip(updateState));
			}
		},
		'PRIVATE#DRAGDROP_START': (coeffects) => {
			const {action, updateState} = coeffects;
			const {target, context} = action.payload;
			// set initial insertion point above
			updateState({
				operation: 'set',
				path: 'dragdropState',
				value: {
					focusId: target.id,
					initialFocusId: target.id,
					cursorAbove: true
				},
				shouldRender: false
			});
			// for keyboard, show initial drag image
			if (context.getType() !== 'mouse') {
				updateDragCursor(context, coeffects, true);
			}
		},
		'PRIVATE#DRAGDROP_ENTER': (coeffects) => {
			const {action, properties, dispatch, host} = coeffects;
			const {target, context} = action.payload;
			if (target.id && target.id != '') {
				const targetPath = getPathToItem(properties.items, target.id);
				const item = getPathItem(properties.items, targetPath);

				// if entering a collapsed item (not on keyboard), schedule auto-expansion
				if (context.getType() !== 'key') {
					const isCollapsed =
						Array.isArray(item.children) &&
						!hasPath(properties.expandedItems, targetPath);
					if (isCollapsed) {
						unschedule('expand');
						schedule(
							'expand',
							() => expandItem(item, targetPath, dispatch, properties),
							AUTO_EXPAND_DELAY
						);
					}
				}
			}
			// dragCursor updated in OVER handler for mouse/touch
			if (!isValidDrop(target, context, properties)) {
				hideDragCursor(host);
			}
		},
		'PRIVATE#DRAGDROP_OVER': (coeffects) => {
			const {action, host} = coeffects;
			const {target, context} = action.payload;
			if (target === host) return;
			const targetBounds = target.getBoundingClientRect();
			const lastDragPos = context.getLastDragPosition();
			const cursorAbove =
				lastDragPos.y - targetBounds.top < targetBounds.height / 2;
			updateDragCursor(context, coeffects, cursorAbove);
		},
		'PRIVATE#DRAGDROP_LEAVE': () => {
			unschedule('expand');
		},
		'PRIVATE#DRAGDROP_DROP': (coeffects) => {
			// if we scheduled a hover-expansion, cancel it
			unschedule('expand');
			handleDrop(coeffects);
			const {host, state, updateState} = coeffects;
			const {
				dragdropState: {initialFocusId}
			} = state;
			schedule(
				'refocus',
				() => {
					const item = host.shadowRoot.querySelector(`#${initialFocusId}`);
					if (item) item.focus();
					updateState({
						operation: 'set',
						path: 'dragdropState',
						value: {focusId: null, initialFocusId: null, cursorAbove: true},
						shouldRender: false
					});
				},
				100
			);
		},
		'PRIVATE#DRAGDROP_END': (coeffects) => cleanupDragDrop(coeffects)
	},
	behaviors: [
		{
			behavior: focusBehavior
		},
		{
			behavior: dragdropBehavior,
			options: {
				dragSources: 'li',
				dropTargets: 'li',
				dragImage: (target) => {
					// clean up focus ring issues with sub-components in the item
					target.focus();
					target.classList.add('droppable');
					const item = target.querySelector('div.now-content-tree-node');
					return item;
				},
				dragCount: (target, coeffects) => {
					const {properties} = coeffects;
					const itemPath = getPathToItem(properties.items, target.id);
					return hasPath(properties.selectedItems, itemPath)
						? properties.selectedItems.length
						: 1;
				},
				dragData: (dragSource, {properties}) => {
					const {items, selectedItems} = properties;
					const itemPath = getPathToItem(items, dragSource.id);
					if (itemPath && itemPath.length) {
						// if the path is in the current selection, move the whole selection. Else just move the item
						const itemPaths = hasPath(selectedItems, itemPath)
							? selectedItems
							: [itemPath];
						return {
							format: 'now-nds/content-tree-item-path',
							data: itemPaths
						};
					}
				}
			}
		}
	],
	dispatches: {
		/**
		 * Dispatched when the user clicks an actionable tree item label. Payload
		 * contains a reference to the clicked item.
		 * @type {{item: Object}}
		 * @uib.description Dispatched when the user clicks an actionable tree item label
		 */
		'NOW_CONTENT_TREE#ACTIONABLE_ITEM_CLICKED': {
			schema: {
				type: 'object',
				properties: {
					item: {type: 'object'}
				}
			}
		},
		/**
		 * Dispatched when the user clicks a tree item. Payload contains a
		 * reference to the clicked item.
		 * @type {{item: Object}}
		 * @uib.description Dispatched when the user clicks a tree item
		 */
		'NOW_CONTENT_TREE#ITEM_CLICKED': {
			schema: {
				type: 'object',
				properties: {
					item: {type: 'object'}
				}
			}
		},
		/**
		 * Dispatched when a user triggers loading of a parent item's children
		 * from the server. Add the item's path to the `loadingItems` list
		 * to show a loading indicator, remove the item's path when its children
		 * are loaded, or mark the loading as failed with `error`.
		 * @type {{path: string[], item: Object}}
		 * @uib.description Dispatched when a user triggers loading of a parent item's children from the server.
		 */
		'NOW_CONTENT_TREE#LOADING_REQUESTED': {
			schema: {
				type: 'object',
				properties: {
					path: {
						type: 'array',
						items: {
							type: 'string'
						}
					},
					item: {type: 'object'}
				}
			}
		},

		/**
		 * Dispatched when a user cancels loading of a parent item's children
		 * from the server. Remove the item's path from the `loadingItems` list
		 * even if it is currently in the error state. If the request can't be
		 * cancelled (for example, if it used `fetch`) and it succeeds later, then handle its
		 * response and add the children without re-expanding the item. If the
		 * request can't be cancelled and it fails later, then don't re-add the
		 * item to `loadingItems` with an error message.
		 * @type {{path: string[], item: Object}}
		 * @uib.description Dispatched when a user cancels loading of a parent item's children from the server.
		 */
		'NOW_CONTENT_TREE#LOADING_CANCELLED': {
			schema: {
				type: 'object',
				properties: {
					path: {
						type: 'array',
						items: {
							type: 'string'
						}
					},
					item: {type: 'object'}
				}
			}
		},
		/**
		 * Dispatched when a user clicks a tree item action icon. Payload
		 * includes a reference to the clicked item and action. Action type can be
		 * overridden by setting `action.clickActionType`.
		 * @type {{item: Object, action: Object, domTarget: HTMLElement, actionTarget: HTMLElement}}
		 * @uib.description Dispatched when a user clicks a tree item action icon
		 */
		'NOW_CONTENT_TREE#ACTION_CLICKED': {
			schema: {
				type: 'object',
				properties: {
					action: {type: 'object'},
					domTarget: {type: 'object'},
					item: {type: 'object'},
					actionTarget: {type: 'object'}
				}
			}
		},
		/**
		 * Dispatched when a user's mouse hovers over a tree item action icon.
		 * Payload includes a reference to the hovered item and action. Action
		 * type can be overridden by setting `action.mouseenterActionType`.
		 * @type {{item: Object, action: Object, domTarget: HTMLElement}}
		 * @uib.description Dispatched when a user's mouse hovers over a tree item action icon.
		 */
		'NOW_CONTENT_TREE#ACTION_MOUSEENTER': {
			schema: {
				type: 'object',
				properties: {
					action: {type: 'object'},
					domTarget: {type: 'object'},
					item: {type: 'object'}
				}
			}
		},
		/**
		 * Dispatched when a user's mouse stops hovering over a tree item action icon.
		 * Payload includes a reference to the hovered item and action. Action
		 * type can be overridden by setting `action.mouseleaveActionType`.
		 * @type {{item: Object, action: Object, domTarget: HTMLElement}}
		 * @uib.description Dispatched when a user's mouse stops hovering over a tree item action icon
		 */
		'NOW_CONTENT_TREE#ACTION_MOUSELEAVE': {
			schema: {
				type: 'object',
				properties: {
					action: {type: 'object'},
					domTarget: {type: 'object'},
					item: {type: 'object'}
				}
			}
		}
	}
});
