/**
 * Replaces all occurrence of `searchTerm` found in `string` with styled DOM
 * nodes. The search is _not_ case-sensitive.
 *
 * @param searchTerm {string} - The substring to look for within the larger
 * `string`
 * @param string {string} - The input string to search in.
 *
 * @returns {Array<string|VDomNode>} - the minimal amount of data needed
 * to render correctly. This may be a string literal (no matches found), or an
 * array of mixed strings + vdom nodes.
 */
export default function highlightString(searchTerm, string) {
	if (!searchTerm) {
		return string;
	}

	// elements in this array will be a mix of types. String literals for the
	// non-highlighted portitions and snabbdom nodes (<mark> tags) for the
	// highlighted portions. snabbdom is perfectly happy rendering this array as
	// children.
	const result = [];
	const lowerString = string.toLowerCase();
	const lowerSearchTerm = searchTerm.toLowerCase();
	const highlightLength = lowerSearchTerm.length;

	let lastIndex = 0;
	let nextIndex = 0;
	while ((nextIndex = lowerString.indexOf(lowerSearchTerm, lastIndex)) !== -1) {
		// lastIndex = beginning of string OR where the previous highlighted string ended
		// nextIndex = where the next highlight string starts
		//
		// if these two values are not equal, it means there is non-highlighted
		// content that we need to deal with.
		//
		// e.g.: on the first iteration
		//
		//       searchTerm = "world"
		//       label = "hello world"
		//                │     │
		//      lastIndex ┘     └ nextIndex
		//
		// Need to push the prefix "hello " into the result.
		if (nextIndex > lastIndex) {
			result.push(string.slice(lastIndex, nextIndex));
		}

		const highlight = (
			<mark>{string.slice(nextIndex, nextIndex + highlightLength)}</mark>
		);
		result.push(highlight);

		lastIndex = nextIndex + highlightLength;
	}

	if (result.length === 0) {
		return string;
	}

	// content between the last highlight and end of the string
	if (lastIndex !== string.length) {
		result.push(string.slice(lastIndex));
	}

	return result;
}
